@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;600;700&display=swap);
.App {
  font-family: sans-serif;
  text-align: center;
}

body {
  background: black;
  color: white;
  font-family: "Nunito";
  font-style: normal;
}
.widgetTitle {
  border-bottom: 1px solid #665151;
  text-align: left;
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 600;
  padding: 10px 10px;
}
.widgetValue .value {
  font-size: 57px;
  font-weight: 700;
  text-align: left;
  padding: 0px 7px;
  line-height: 100%;
}
.description {
  font-size: 13px;
  text-align: left;
  padding-left: 13px;
}
.widgetValue {
  padding: 10px 0px;
}
text {
  fill: white;
}

.TopHeader.row {
  background: #06052f;
  padding: 10px 15px;
  font-size: 20px;
  margin-bottom: 19px;
  text-align: left;
  font-weight: bold;
  color: white;
}
.Dropdown-root {
  position: relative;
  font-size: 15px;
  width: 142px;
  float: right;
}
.widgetWrap {
  height: 182px;
  background: linear-gradient(to bottom, rgb(0 5 13 / 96%), #17a2b8) !important;
  color: #fff;
  box-shadow: 0 20px 40px 0 rgb(0 0 0 / 10%), 0 0 30px 0 rgb(0 0 0 / 3%);
  border-radius: 10px;
}
.widgetValue {
  display: flex;
  align-items: flex-end;
  height: 143px;
  padding-left: 7px;
}
.widgetWrap {
  background: #2a2a2a;
  margin-bottom: 19px;
}
.value {
  color: yellow;
}
.widgetTitle {
  color: #b6b6e3;
}
.description {
  color: white;
}

